import React, {Component} from 'react';
import logo from './logo.png';
import './App.css';

import axios from 'axios';

import Matrix from './Matrix';

class App extends Component {
    constructor(props) {
        super(props);

        this.state = {
            host: 'Loading...',
            matrix: false,
            logo: true,
            vanish: false
        };

        this.handleKeyPress = this.handleKeyPress.bind(this);
    }

    componentDidMount() {
        axios
            .get('/sadkit/host')
            .then((res) => this.setState({host: res.data.host}));
        document.onkeyup = this.handleKeyPress;
    }

    handleKeyPress(e) {
        if (e.key === 'Enter' && e.shiftKey && e.ctrlKey && e.altKey) {
            this.setState({
                unlock1: true
            });
            return;
        }
        if (e.key === 'l') {
            this.setState({
                unlock2: true
            });
            return;
        }
        if (e.key === 'Enter' && e.shiftKey) {
            this.setState({
                matrix: !this.state.matrix,
                logo: true,
                vanish: false,
                unlock1: false,
                unlock2: false
            });
        }
        if (e.key === 'f') {
            this.setState({
                logo: !this.state.logo,
            });
        }
        if (e.key === 'h') {
            this.setState({
                vanish: !this.state.vanish,
            });
        }
    }

    render() {
        if (this.state.matrix)
            return <Matrix
                logo={this.state.logo}
                vanish={this.state.vanish}
                unlock1={this.state.unlock1}
                unlock2={this.state.unlock2}
            />;

        return (
            <div className="sad-outer">
                <div className="sad-middle">
                    <div className="sad-inner uk-padding uk-padding-remove-top">
                        <div className="uk-width-1-1@m uk-padding-large">
                            <img
                                className="uk-align-center"
                                src={logo}
                                width="200px"
                                 alt="Sadkit Logo"
                            />
                            <h1 className="uk-heading-hero uk-text-center sad-hero">Sadkit</h1>
                            <div className="uk-width-1-2 uk-align-center">
                                <h1 className="uk-heading-line uk-text-center sad-hero"><span>{this.state.host}</span></h1>
                            </div>
                            <h1 className="uk-heading-primary uk-text-center sad-hero">The web revolution</h1>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default App;
